// 'use client'

import Link from 'next/link';
import { useState } from 'react';

export default function Navbar() {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <nav className="bg-white shadow-lg">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className="flex justify-between h-16">
                    <div className="flex">
                        {/* Logo */}
                        <div className="flex-shrink-0 flex items-center">
                            <Link href="/" className="text-2xl font-bold text-blue-600">
                                VeriStream
                            </Link>
                        </div>

                        {/* Desktop Navigation */}
                        <div className="hidden sm:ml-6 sm:flex sm:space-x-8">
                            <Link href="/" className="text-gray-900 hover:text-blue-600 px-3 py-2">
                                Home
                            </Link>
                            <Link href="/how-it-works" className="text-gray-900 hover:text-blue-600 px-3 py-2">
                                How It Works
                            </Link>
                            <Link href="/analyze" className="text-gray-900 hover:text-blue-600 px-3 py-2">
                                Analyze Content
                            </Link>
                        </div>
                    </div>

                    {/* Mobile Menu Button */}
                    <div className="-mr-2 flex items-center sm:hidden">
                        <button
                            onClick={() => setIsOpen(!isOpen)}
                            className="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-gray-500"
                        >
                            {isOpen ? 'Close' : 'Menu'}
                        </button>
                    </div>
                </div>
            </div>

            {/* Mobile Menu */}
            {isOpen && (
                <div className="sm:hidden">
                    <div className="pt-2 pb-3 space-y-1">
                        <Link href="/" className="text-gray-900 block px-3 py-2">
                            Home
                        </Link>
                        <Link href="/how-it-works" className="text-gray-900 block px-3 py-2">
                            How It Works
                        </Link>
                        <Link href="/analyze" className="text-gray-900 block px-3 py-2">
                            Analyze Content
                        </Link>
                    </div>
                </div>
            )}
        </nav>
    );
}